* {
  margin: 0;
  padding: 0;
  font-family: Quantico;
}

html {
  min-height: 100%;
  position: relative;
}

.contenedor-imagen {
  align-items: center;
  display: flex;
}

.contenedor-imagen img {
  width: 100%;
}

.contenedor-paneles {
  width: 100%;
  background-color: #fff;
  justify-content: center;
  padding: 4rem;
  display: inline-flex;
  overflow: hidden;
}

#titulo {
  z-index: 100;
  width: 100%;
  position: absolute;
}

h1 {
  text-align: center;
  color: #fff;
  font-size: 4rem;
}

.carta {
  width: 20rem;
  color: #fff;
  background-color: #27282f;
  border-radius: 2.5rem;
  margin: 2.5em;
  transition: transform .2s;
}

.carta h3 {
  color: #fff;
  text-align: center;
  margin: 1em;
  font-size: 2em;
}

.carta h4 {
  text-align: center;
  margin-bottom: 2rem;
  font-size: 1.5em;
}

.carta ul li {
  margin-top: .5rem;
  margin-left: 3rem;
  list-style-type: square;
}

.carta:hover {
  transform: scale(1.1);
}

.contratar {
  color: #000;
  cursor: pointer;
  border-radius: 1.5rem;
  justify-content: center;
  margin: auto;
  padding: .5rem 1rem;
  transition: all .5s;
  display: flex;
}

.contratar:hover {
  color: #000;
  background-color: gold;
  transition: all .5s;
}

.contratar-1 {
  margin-top: 7rem;
  margin-bottom: 2rem;
}

.contratar-2 {
  margin-top: 3.8rem;
  margin-bottom: 2rem;
}

.contratar-3 {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

@media (max-width: 480px) {
  .contenedor-imagen {
    width: 100%;
  }

  .contenedor-imagen h1 {
    font-size: 1.5rem;
  }

  .contenedor-titulo h2 {
    text-align: center;
    margin-top: 1.5rem;
    font-size: 1.2rem;
  }

  .contenedor-paneles {
    flex-direction: column;
    align-items: center;
  }

  .carta {
    width: 200px;
    margin: 2em;
  }

  .carta h3 {
    font-size: 1.8rem;
  }

  .carta h4 {
    font-size: 1.5rem;
  }

  .carta ul li {
    margin-top: .5rem;
    margin-left: 1.5rem;
    font-size: 1rem;
  }

  .contratar-1, .contratar-2, .contratar-3 {
    margin-top: 3rem;
    margin-bottom: 2rem;
  }
}

@media (min-width: 481px) and (max-width: 779px) {
  .contenedor-imagen {
    width: 100%;
  }

  .contenedor-imagen img {
    width: 100%;
    height: 400px;
  }

  .contenedor-imagen h1 {
    font-size: 2rem;
  }

  .contenedor-titulo h2 {
    text-align: center;
    margin-top: 1.5rem;
    font-size: 1.8rem;
  }

  .contenedor-paneles {
    flex-direction: column;
    align-items: center;
    margin-bottom: 4rem;
  }

  .carta {
    width: 300px;
    margin: 2em;
  }

  .carta h3, .carta h4 {
    font-size: 1.5rem;
  }

  .carta ul li {
    margin-top: .5rem;
    margin-left: 1.5rem;
    font-size: 1rem;
  }

  .contratar-1, .contratar-2, .contratar-3 {
    margin-top: 3rem;
    margin-bottom: 2rem;
  }
}

@media (min-width: 780px) {
  .contenedor-imagen {
    width: 100%;
  }

  .contenedor-imagen img {
    width: 100%;
    height: 400px;
  }

  .contenedor-imagen h1 {
    font-size: 2.5rem;
  }

  .contenedor-titulo h2 {
    text-align: center;
    margin-top: 1.8rem;
    font-size: 2rem;
  }

  .contenedor-paneles {
    width: 100%;
    background-color: #fff;
    justify-content: center;
