  }

  .contratar-1, .contratar-2, .contratar-3 {
    margin-top: 3rem;
    margin-bottom: 2rem;
  }
}

@media (min-width: 1200px) {
  .contenedor-imagen {
    width: 100%;
  }

  .contenedor-imagen h1 {
    font-size: 3rem;
  }

  .contenedor-titulo h2 {
    text-align: center;
    margin-top: 1.8rem;
    font-size: 2.5rem;
  }

  .carta {
    width: 300px;
    margin-bottom: 2em;
  }

  .carta h3 {
    font-size: 1.8rem;
  }

  .carta h4 {
    font-size: 1.5rem;
  }

  .carta ul li {
    margin-top: .5rem;
    margin-left: 1.5rem;
    font-size: 1rem;
  }

  .contratar-1, .contratar-2, .contratar-3 {
    margin-top: 3rem;
    margin-bottom: 2rem;
  }

  h1 {
    color: #fff;
    font-size: 2rem;
    position: absolute;
  }
}

@media (min-width: 2000px) {
  .contenedor-imagen {
    width: 100%;
  }

  .contenedor-imagen h1 {
    font-size: 4rem;
  }

  .contenedor-titulo h2 {
    text-align: center;
    margin-top: 2rem;
    font-size: 3.1rem;
  }

  .carta {
    width: 600px;
    margin-bottom: 5em;
  }

  .carta h3, .carta h4 {
    font-size: 2.8rem;
  }

  .carta ul li {
    margin-top: .5rem;
    margin-left: 4rem;
    font-size: 2rem;
  }

  .contratar-1, .contratar-2, .contratar-3 {
    margin-top: 3rem;
    margin-bottom: 2rem;
  }

  h1 {
